*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    min-height: 100vh;
    max-height: max-content;
    font-family: 'Karla', sans-serif;
    background-image: url("./images/blob-yellow.png"), url("./images/blob-blue.png");
    background-repeat: no-repeat;
    background-position: right top, left bottom;
}

.main-menu{
    height: 100vh;
    display: flex;
    gap: 1rem;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
}

.main-menu-off{
    display: none;
}

.main-menu h2{
    font-weight: 300;
}

.main-menu button{
    font-family: 'Karla', sans-serif;
    padding: 1rem 2rem;
    background-color: #4D5B9E;
    color: white;
    border: none;
    border-radius: 10px;
    cursor: pointer;
}

main{
    height: max-content;
    display: flex;
    justify-content: center;
    align-items: center;
}

.quizz{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    padding: 1rem 0;
}

.quizz-question{
    width: 800px;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    margin: 1rem 0 0 0;
}

.quizz-question h2{
    color: #293264;
}

.quizz-options{
    display: flex;
    flex-direction: row;
    list-style-type: none;
    gap: 1rem;
    justify-content: flex-start;
}

.quizz-options button{
    padding: .5rem 2rem;
    border: none;
    border: 1.5px solid #4D5B9E;
    background: none;
    border-radius: 10px;
    color: #293264;
    font-weight: 700;
    font-family: 'Karla', sans-serif;
    cursor: pointer;
}

.quizz-options .active-btn{
    background-color: #D6DBF5;
    border: none;
    color: #293264;
}

.quizz-options .right-btn{
    background-color: #94D7A2;
    border: none;
    color: #293264;
}

.quizz-options .false-btn{
    background-color: #F8BCBC;
    border: none;
    color: #293264;
    opacity: .5;
}

.quizz-options .desactive-btn{
    opacity: .5;
}

.quizz-options .btn-none{
    display: none;
}

hr{
    color: hsla(231, 41%, 90%, 0.5);
    margin-top: 1rem;
}

.check-answers{
    background-color: #4D5B9E;
    border: none;
    border-radius: 15px;
    font-family: 'Karla', sans-serif;
    color: white;
    padding: 1rem 2rem;
    font-weight: 700;
    cursor: pointer;
}

.quizz-answers{
    display: flex;
    gap: 1rem;
    align-items: center;
    flex-direction: row;
}

.quizz-answers-text{
    display: none;
}

.main-close{
    display: none;
}

form{
    display: flex;
    flex-direction: row;
    gap: 2rem;
}

form div{
    display: flex;
    flex-direction: column;
    gap: .5rem;
}

.form-nbQuestions input{
    padding: .5rem 1rem;
    border-radius: 10px;
    border: solid 1px #293264;
}

.form-category select,
.form-difficulty select{
    padding: .5rem 1rem;
    border-radius: 10px;
    border: solid 1px #293264;
    background: none;
}